const serviceCards = document.querySelectorAll('.jsCardService');

for (let index = 0; index < serviceCards.length; index++) {
    const element = serviceCards[index];
    let trigger = element.querySelector('.jsCardServiceTrigger');

    trigger.addEventListener('click', function(){

        if(element.classList.contains('is-active')){
            removeClasses()

        } else{
            removeClasses()
            element.classList.add('is-active');
        }
    })

    function removeClasses(){
        let cards = document.querySelectorAll('.jsCardService');

        for (let index = 0; index < cards.length; index++) {
            const element = cards[index];
            element.classList.remove('is-active');
        }
    }
}