const jsSliderReviews = new Swiper('.jsSliderReviews .slider__inner', {
	modules: [Navigation, Pagination, FreeMode, EffectCreative],

	pagination: {
		el: ".jsSliderReviews .swiper-pagination",
		clickable: true,
		bulletClass: "slider__pagination-item",
	},

	loop: true,
	// freeMode: true,
	grabCursor: true,
	effect: "creative",
	slidesPerView: "auto",
	speed: 750,
	creativeEffect: {
		limitProgress: 2,
		prev: {
			translate: ["-125%", 0, 2],
		},
		next: {
			translate: ["50%", 0, -180],
		}
	}
});

const jsSliderAboutServices  = new Swiper('.jsSliderAboutServices .slider__inner', {
	modules: [Navigation, Pagination, FreeMode, EffectCreative],

	loop: true,
	// freeMode: true,
	grabCursor: true,
	effect: "creative",
	slidesPerView: "auto",
	speed: 750,
	creativeEffect: {
		limitProgress: 3,
		prev: {
			translate: ["-125%", 0, 2],
		},
		next: {
			translate: ['30%', 0, 0],
		}
	}
});
