        const bitrixItems = document.querySelectorAll('.section-bitrix__item');
        const jsBitrixTitle = document.querySelector('.jsBitrixTitle');
        const sectionBitrix = document.querySelector('.section-bitrix');

    
        tl.to(jsBitrixTitle,
            {
                className: 'is-swapped',
                delay: 1,
                scrollTrigger: {
                    trigger: jsBitrixTitle,
                    start: 'top 75%',
                    scrub: true,
                },
            }
        )
    
