
document.addEventListener('DOMContentLoaded', function(){
    if(document.querySelector('.jsPreloader')){
        let preloader               = document.querySelector('.jsPreloader');
        let preloaderPercentage     = document.querySelector('.jsPreloaderPercentage');
        let preloaderShadow     = document.querySelector('.jsPreloaderShadow');
        let imagesCount             = document.querySelectorAll('img').length;
        let body                    = document.querySelector('body');
        let percent                 = 100 / imagesCount;
        let progress                = 0;
        let loadedImg               = 0;
    
        body.classList.add('_lock');
        preloaderShadow.style.opacity = '0';
    
        setTimeout(() => {
    
            if(imagesCount != 0){
                for (var i = 0; i < imagesCount; i++) {
                    var img_copy        = new Image();
                    img_copy.src        = document.images[i].src;
                    img_copy.onload     = img_load;
                    img_copy.onerror    = img_load;
                }
            } else{
                progress = 100;
                loadedImg;
        
                unlock(progress, loadedImg)
            }
    
        }, 500);
    
        
        function img_load () {
            progress += percent;
            loadedImg++;
    
            unlock(progress, loadedImg)
        }
        
        function unlock(progress, loadedImg){
            preloaderPercentage.innerHTML = Math.round(progress) + ' %'
            preloaderShadow.style.opacity = (Math.round(progress)/100)
    
        
            if (progress >= 100 || loadedImg == imagesCount) {
        
                setTimeout(() => {
                    preloader.classList.add('is-loaded');
                    body.classList.remove('_lock');
                }, 250);
            }
        }
    }
})